    },
    submit() {
      this.loading = true;
      let data = {};
      if (this.type == "post") {
        data.post_id = this.postId;
      } else if (this.type == "comment") {
        data.comment_id = this.commentId;
      }
      axios
        .post("/reportpost", data)
        .then((response) => {
